import gql from 'graphql-tag';
const ADD_USER = gql`
  mutation(
    $name: String!
    $email: String!
    $password: String!
    $role: USER_ROLES_enum
  ) {
    insert_Users_one(
      object: { name: $name, email: $email, password: $password, role: $role }
    ) {
      id
      name
      email
      created
      role
      password
    }
  }
`;
const UPDATE_USER = gql`
  mutation(
    $id: String!
    $name: String
    $email: String
    $role: USER_ROLES_enum
    $password: String
  ) {
    update_Users(
      where: { id: { _eq: $id } }
      _set: { name: $name, email: $email, role: $role, password: $password }
    ) {
      affected_rows
      returning {
        id
        name
        email
        role
        password
      }
    }
  }
`;
const DELETE_USER = gql`
  mutation($id: String!) {
    delete_Users(where: { id: { _eq: $id } }) {
      affected_rows
      returning {
        id
        name
        email
        role
      }
    }
  }
`;
